@import url(https://fonts.googleapis.com/css?family=Montserrat:900i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Red+Hat+Display:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Red+Hat+Display:900&display=swap);
body, html {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: black;
}
.nav-wrap {
    width: 130px;
    height: 100%;
    background-color: rgb(34, 32, 32);
    border-right: 1px solid rgb(83, 81, 81);
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.logo-letter {
    margin: 10px 13px 0px 0px;
    font-family: 'Montserrat', sans-serif;
  font-size: 70px;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: white;
  text-shadow: 
						8px 8px #ff1f8f,
						15px 15px #1be0af;
}
.donut-wrap {
    font-family: serif;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: black;
    color:  #f1ee39;
    font-size:10px;
    white-space: pre;
    letter-spacing: 5px;
    line-spacing: 0px;
}

p {
    margin: 0px;
    padding: 0px;
}
.landing-wrap {
    color: white;
    font-family: 'Red Hat Display', serif;
    font-size: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}
.intro-wrap {
    margin-right: 300px;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100vw;
  height: 100vh;
}

